import { Card, CardContent, CardMedia, CardActions, Typography, Button } from "@mui/material";
import { Masonry } from "@mui/lab";
import { useState } from "react";
import bio from "../../asset/bio.json"
import ProjectDetail from "./ProjectDetail";

export default function Project() {

    const [modalDetail, setModalDetail] = useState({ isOpen: false, data: {} })

    const openModal = (data: any) => {
        setModalDetail({
            isOpen: true,
            data: data
        })
    }

    const closeModal = () => {
        setModalDetail({
            isOpen: false,
            data: {}
        })
    }

    return (
        <div id="portfolio" className="project">
            <ProjectDetail modalDetail={modalDetail} onClose={closeModal} />
            <br />
            <Typography variant="h4" align="center">
                PORTOFOLIO
            </Typography>
            <br />
            <br />
            <Masonry columns={{ xs: 2, sm: 3, md: 4, lg: 5 }} spacing={2}>
                {bio.projects.map((project: any, i: number) => (
                    <Card key={i}>
                        <CardMedia
                            component="img"
                            image={require("../../asset/img/" + project.img[0])}
                        />
                        <CardContent className="card-content">
                            <Typography gutterBottom variant="h5" component="div">
                                {project.name}
                            </Typography>
                            <Typography gutterBottom variant="h6" component="div">
                                Tahun {project.year}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                {project.skills.map((skill: any, i: number) => (
                                    <span key={i}>
                                        {skill}{", "}
                                    </span>
                                ))}
                            </Typography>
                        </CardContent>
                        <CardActions className="card-footer">
                            <Button onClick={() => openModal(project)}>Detail</Button>
                        </CardActions>
                    </Card>
                ))}
            </Masonry>
        </div>
    )
}