import { Close } from "@mui/icons-material";
import { Typography, Modal, Box, IconButton } from "@mui/material";
import Carousel from 'react-material-ui-carousel'

export default function ProjectDetail(props: any) {
    return (
        <Modal
            open={props.modalDetail.isOpen}
            onBackdropClick={props.onClose}
        >
            {props.modalDetail.data.img
                ? (
                    <Box className="modal-template">
                        <Box className="modal-header">
                            <Typography variant="h5">
                                Detail Project {props.modalDetail.data.name}
                            </Typography>
                            <IconButton onClick={props.onClose}>
                                <Close />
                            </IconButton>
                        </Box>
                        <Box className="modal-content">
                            <Carousel
                                className="carousel"
                                navButtonsAlwaysVisible={true}
                            >
                                {props.modalDetail.data.img.map((img: any, i: number) => (
                                    <Box className="image" key={i}>
                                        <img src={require("../../asset/img/" + img)}/>
                                    </Box>
                                ))}
                            </Carousel>
                            <Typography gutterBottom variant="h6" component="div">
                                Tahun {props.modalDetail.data.year}
                            </Typography>
                            <span>
                                {props.modalDetail.data.description}
                            </span>
                        </Box>
                    </Box>
                )
                : (
                    <></>
                )}
        </Modal>
    )
}