import { Menu } from "@mui/icons-material";
import { IconButton, List, ListItemButton, Popover } from "@mui/material";
import { useState } from "react";

export default function Nav() {

    const [popMenuAnchor, setPopMenuAnchor] = useState<HTMLButtonElement | null>(null);

    const handleMenu = (section: string) => {
        setPopMenuAnchor(null)
        let x = setTimeout(() => {
            let tempLink = document.createElement('a')
            tempLink.href = `#${section}`
            tempLink.click();
            clearTimeout(x)
        }, 100)
    }

    return (
        <div className="nav">
            <ul>
                <li><a href="#home">HOME</a></li>
                <li><a href="#biodata">BIODATA</a></li>
                <li><a href="#portfolio">PORTOFOLIO</a></li>
                <li><a href="#scholl">PENDIDIKAN</a></li>
                <li><a href="#contact">KONTAK</a></li>
            </ul>
            <Popover
                open={Boolean(popMenuAnchor)}
                anchorEl={popMenuAnchor}
                onClose={() => setPopMenuAnchor(null)}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
            >
                <List>
                    <ListItemButton onClick={() => handleMenu("home")}>HOME</ListItemButton>
                    <ListItemButton onClick={() => handleMenu("biodata")}>BIODATA</ListItemButton>
                    <ListItemButton onClick={() => handleMenu("portfolio")}>PORTOFOLIO</ListItemButton>
                    <ListItemButton onClick={() => handleMenu("scholl")}>PENDIDIKAN</ListItemButton >
                    <ListItemButton onClick={() => handleMenu("contact")}>KONTAK</ListItemButton >
                </List >
            </Popover >
            <IconButton className="burger" onClick={e => setPopMenuAnchor(e.currentTarget)}>
                <Menu />
            </IconButton>
        </div >
    )
}